"use client";
import { titleCaseName, canonicalKey } from "@/lib/names";
import type { UseFormRegister, UseFormSetValue, FieldErrors } from "react-hook-form";
import type { RegisterValues } from "@/lib/register-schemas";

export default function NameFields({
  register,
  setValue,
  errors,
}: {
  register: UseFormRegister<RegisterValues>;
  setValue: UseFormSetValue<RegisterValues>;
  errors: FieldErrors<RegisterValues>;
}) {
  return (
    <div className="grid gap-4 sm:grid-cols-3">
      <Field label="Given name" err={errors.givenName?.message as string | undefined}>
        <input
          {...register("givenName")}
          className="mt-1 w-full rounded border border-slate-300 p-2"
          onBlur={(e) => {
            const t = titleCaseName(e.target.value);
            setValue("givenName", t, { shouldValidate: true });
            setValue("givenNameKey", canonicalKey(t));
          }}
        />
      </Field>
      <Field label="Middle (optional)" err={errors.middleName?.message as string | undefined}>
        <input
          {...register("middleName")}
          className="mt-1 w-full rounded border border-slate-300 p-2"
          onBlur={(e) => {
            const t = titleCaseName(e.target.value);
            setValue("middleName", t);
          }}
        />
      </Field>
      <Field label="Surname" err={errors.surname?.message as string | undefined}>
        <input
          {...register("surname")}
          className="mt-1 w-full rounded border border-slate-300 p-2"
          onBlur={(e) => {
            const t = titleCaseName(e.target.value);
            setValue("surname", t, { shouldValidate: true });
            setValue("surnameKey", canonicalKey(t));
          }}
        />
      </Field>
    </div>
  );
}

function Field({ label, err, children }: { label: string; err?: string; children: React.ReactNode }) {
  return (
    <label className="block">
      <span className="block text-sm text-slate-700">{label}</span>
      {children}
      {err && <p className="text-xs text-rose-600">{err}</p>}
    </label>
  );
}

