import countries from "i18n-iso-countries";
import en from "i18n-iso-countries/langs/en.json";

// i18n-iso-countries locale JSON typing workaround
type LocaleData = { locale: string; countries: Record<string, string> };
countries.registerLocale(en as unknown as LocaleData);

export type Country = { code: string; name: string };

export function allCountries(): Country[] {
  const names = countries.getNames("en", { select: "official" }) as Record<string, string>;
  return Object.entries(names)
    .map(([code, name]) => ({ code, name }))
    .sort((a, b) => a.name.localeCompare(b.name));
}

export function isCountry(code?: string): boolean {
  if (!code) return false;
  try {
    return Boolean(countries.alpha2ToNumeric(code.toUpperCase()))
  } catch {
    return false;
  }
}
