"use client";
import { formatLicence } from "@/lib/license-rules";
import type { UseFormRegister, UseFormSetValue, UseFormWatch, FieldErrors } from "react-hook-form";
import type { RegisterValues } from "@/lib/register-schemas";

export default function LicenceInput({
  register,
  setValue,
  watch,
  errors,
}: {
  register: UseFormRegister<RegisterValues>;
  setValue: UseFormSetValue<RegisterValues>;
  watch: UseFormWatch<RegisterValues>;
  errors: FieldErrors<RegisterValues>;
}) {
  const jurisdiction = watch("jurisdiction") as string;
  return (
    <label className="block">
      <span className="block text-sm text-slate-700">Licence number</span>
      <input
        {...register("licenseNo")}
        className="mt-1 w-full rounded border border-slate-300 p-2 font-mono"
        placeholder={maskHint(jurisdiction)}
        onInput={(e) => {
          const t = e.target as HTMLInputElement;
          const newVal = formatLicence(jurisdiction, t.value);
          t.value = newVal;
          setValue("licenseNo", newVal, { shouldValidate: false });
        }}
      />
      {errors.licenseNo?.message && <p className="text-xs text-rose-600">{errors.licenseNo.message as string}</p>}
      <p className="mt-1 text-xs text-slate-500">Type letters/numbers only—formatting is automatic.</p>
    </label>
  );
}
function maskHint(j: string) {
  switch (j) {
    case "CA-ON":
      return "A####-#####-#####";
    case "CA-BC":
      return "####### or ########";
    case "CA-AB":
      return "######-### or 5–9 digits";
    case "CA-SK":
      return "########";
    case "CA-QC":
      return "A############";
    case "CA-NL":
      return "A#########";
    default:
      return "Up to 15 letters/digits";
  }
}

