import { z } from "zod";
import { isValidLicence, normalizedLicence } from "@/lib/license-rules";
import { detectCA, isUSZip } from "@/lib/postal";
import { toE164 } from "@/lib/phone-format";
import { classesFor } from "@/lib/license-classes";
import { passportPatternFor, isValidPassport, normalizePassport } from "@/lib/passport/validate";
import { isCountry } from "@/lib/countries";

const dateISO = z.string().refine((v) => /^\d{4}-\d{2}-\d{2}$/.test(v), "Use YYYY-MM-DD");

export const RegisterSchema = z
  .object({
    // Names (required)
    givenName: z.string().min(1, "Enter given name").max(60),
    surname: z.string().min(1, "Enter surname").max(60),
    middleName: z.string().optional(),
    givenNameKey: z.string().optional(),
    surnameKey: z.string().optional(),

    // Jurisdiction & licence (required)
    jurisdiction: z.enum(
      ["CA-AB", "CA-BC", "CA-MB", "CA-NB", "CA-NL", "CA-NS", "CA-ON", "CA-PE", "CA-QC", "CA-SK"],
      { message: "Select issuing authority" },
    ),
    licenseNo: z.string().min(3, "Enter licence number"),
    licenseClass: z.string().min(1, "Enter class").max(5),
    airBrake: z.boolean().optional(),

    licenseExpiry: dateISO,
    dob: dateISO,
    gender: z.enum(["Male", "Female", "Non-binary", "Prefer not to say"]).optional(),

    // Contact
    email: z.string().email(),
    phone: z.string().min(7),
    phoneCountry: z.enum(["CA", "US"]).default("CA"),

    // Address (postal required; others optional)
    address1: z.string().optional(),
    address2: z.string().optional(),
    city: z.string().optional(),
    region: z.string().optional(),
    postal: z.string().min(3, "Enter postal/ZIP"),
    country: z.string().optional(),

    // Status wizard
    citizenship: z.enum(["CA", "US", "Other"]).default("CA"),
    residencyCA: z.enum(["Citizen", "PR", "Work Permit", "Study Permit", "Visitor"]).default("Citizen"),
    prNumber: z.string().optional(),
    prExpiry: dateISO.optional(),
    permitType: z.string().optional(),
    permitExpiry: dateISO.optional(),
    uci: z.string().optional(),
    passportCountry: z.string().optional(),
    passportNumber: z.string().optional(),
    passportExpiry: dateISO.optional(),

    englishLevel: z.enum(["Basic", "Conversational", "Fluent", "Native"]),
    otherLanguages: z
      .array(
        z.object({
          lang: z.string().min(2),
          level: z.enum(["Basic", "Conversational", "Fluent", "Native"]),
        }),
      )
      .max(10)
      .optional(),

    work: z
      .array(
        z.object({
          company: z.string().optional(),
          role: z.string().optional(),
          start: dateISO.optional(),
          end: dateISO.optional(),
        }),
      )
      .max(10)
      .optional(),

    consentAbstract: z.boolean().refine((v) => v === true, { message: "Consent is required" }),
    consentName: z.string().min(2, "Type your name to sign"),
    consentDate: dateISO,
    certifyAccurate: z.boolean().refine((v) => v === true, { message: "You must certify accuracy" }),

    // Documents
    docLicenseFront: z.instanceof(File).optional(),
    docLicenseBack: z.instanceof(File).optional(),
    docPassport: z.instanceof(File).optional(),
    docPR: z.instanceof(File).optional(),
    docPermit: z.instanceof(File).optional(),
  })
  .superRefine((v, ctx) => {
    // Age >= 16, expiry >= today
    const today = new Date();
    const yyyy = today.getFullYear();
    const mm = today.getMonth();
    const dd = today.getDate();
    const minDob = new Date(yyyy - 16, mm, dd);
    const dob = new Date(v.dob);
    const exp = new Date(v.licenseExpiry);
    if (isNaN(dob.getTime()) || dob > minDob)
      ctx.addIssue({ code: "custom", path: ["dob"], message: "Driver must be at least 16 years old" });
    if (isNaN(exp.getTime()) || exp < new Date(yyyy, mm, dd))
      ctx.addIssue({ code: "custom", path: ["licenseExpiry"], message: "Expiry must be in the future" });

    // Licence mask by jurisdiction
    if (!isValidLicence(v.jurisdiction, v.licenseNo)) {
      ctx.addIssue({ code: "custom", path: ["licenseNo"], message: "Number doesn’t match jurisdiction format" });
    }
    // Licence class allowed for jurisdiction
    const allowed = classesFor(v.jurisdiction);
    if (allowed.length > 0 && !allowed.includes(v.licenseClass)) {
      ctx.addIssue({ code: "custom", path: ["licenseClass"], message: `Select a valid class for ${v.jurisdiction}` });
    }

    // Phone → must parse to E.164
    if (!toE164(v.phone, v.phoneCountry)) {
      ctx.addIssue({ code: "custom", path: ["phone"], message: "Enter a valid phone number" });
    }

    // Postal logic
    const ca = detectCA(v.postal);
    if (ca.ok) {
      // ok
    } else if (!isUSZip(v.postal)) {
      ctx.addIssue({ code: "custom", path: ["postal"], message: "Enter a valid Canadian postal code or US ZIP" });
    }

    // Status wizard rules
    if (v.citizenship === "CA") {
      if (v.residencyCA !== "Citizen") {
        ctx.addIssue({ code: "custom", path: ["residencyCA"], message: "Canadian citizens must select Citizen" });
      }
    } else {
      // Non-Canadian → passport required for all residencies
      ("passportCountry,passportNumber,passportExpiry".split(",") as string[]).forEach((k) => {
        const rec = v as unknown as Record<string, unknown>;
        const val = rec[k];
        if (!val || (typeof val === "string" && !val.trim())) ctx.addIssue({ code: "custom", path: [k], message: "Required" });
      });
      if (v.passportCountry && !isCountry(v.passportCountry)) {
        ctx.addIssue({ code: "custom", path: ["passportCountry"], message: "Select a valid country" });
      }
      if (v.passportCountry && v.passportNumber && !isValidPassport(v.passportCountry, v.passportNumber)) {
        const pat = passportPatternFor(v.passportCountry);
        ctx.addIssue({ code: "custom", path: ["passportNumber"], message: `Format doesn’t match (${pat.hint})` });
      }
      // Permits need extra fields
      if (["Work Permit", "Study Permit"].includes(v.residencyCA)) {
        ("permitType,permitExpiry".split(",") as string[]).forEach((k) => {
          const rec = v as unknown as Record<string, unknown>;
          const val = rec[k];
          if (!val || (typeof val === "string" && !val.trim())) ctx.addIssue({ code: "custom", path: [k], message: "Required" });
        });
      }
    }

    // PR path → PR number exactly 10 digits
    if (v.residencyCA === "PR") {
      const pr = (v.prNumber ?? "").replace(/\D/g, "");
      if (pr.length !== 10) {
        ctx.addIssue({ code: "custom", path: ["prNumber"], message: "PR card number must be exactly 10 digits" });
      }
      if (!v.prExpiry?.trim()) ctx.addIssue({ code: "custom", path: ["prExpiry"], message: "PR expiry required" });
    }

    // Work/Study → UCI required with 8 or 10 digits (hyphens allowed)
    if (["Work Permit", "Study Permit"].includes(v.residencyCA)) {
      const uci = (v.uci ?? "").replace(/\D/g, "");
      if (!(uci.length === 8 || uci.length === 10)) {
        ctx.addIssue({ code: "custom", path: ["uci"], message: "UCI must be 8 or 10 digits (hyphens allowed)" });
      }
    }

    // Documents: validate size/type and conditional requiredness
    const need = {
      licenseFront: true,
      licenseBack: false,
      pr: v.residencyCA === "PR",
      passport: v.citizenship !== "CA",
      permit: ["Work Permit", "Study Permit"].includes(v.residencyCA),
    } as const;
    ("docLicenseFront,docLicenseBack,docPassport,docPR,docPermit".split(",") as string[]).forEach((k) => {
      const rec = v as unknown as Record<string, unknown>;
      const f = rec[k] as File | undefined;
      if (f && (!["image/jpeg", "image/png"].includes(f.type) || f.size > 4 * 1024 * 1024)) {
        ctx.addIssue({ code: "custom", path: [k], message: "Upload JPG/PNG ≤ 4MB" });
      }
    });
    function req(path: string, file: File | undefined, label: string) {
      if (!file) ctx.addIssue({ code: "custom", path: [path], message: `${label} is required` });
    }
    if (need.licenseFront) req("docLicenseFront", v.docLicenseFront as File | undefined, "Licence (front)");
    if (need.licenseBack) req("docLicenseBack", v.docLicenseBack as File | undefined, "Licence (back)");
    if (need.pr) req("docPR", v.docPR as File | undefined, "PR card image");
    if (need.passport) req("docPassport", v.docPassport as File | undefined, "Passport image");
    if (need.permit) req("docPermit", v.docPermit as File | undefined, "Permit document image");
  });

export type RegisterValues = z.infer<typeof RegisterSchema>;

// Helpers you can use at submit time
export function normalizeForStorage<T extends { licenseNo: string; passportCountry?: string; passportNumber?: string; prNumber?: string; uci?: string }>(
  values: T,
): T {
  return {
    ...values,
    licenseNo: normalizedLicence(values.licenseNo),
    passportNumber: values.passportCountry ? normalizePassport(values.passportNumber ?? "") : undefined,
    prNumber: values.prNumber ? values.prNumber.replace(/\D/g, "") : undefined,
    uci: values.uci ? values.uci.replace(/\D/g, "") : undefined,
  } as T;
}
