"use client";
import { useMemo, useState } from "react";
import { allLanguages } from "@/lib/languages";

type Item = { lang: string; level: "Basic" | "Conversational" | "Fluent" | "Native" };

export default function LanguageMulti({
  value,
  onChange,
}: {
  value: Item[];
  onChange: (items: Item[]) => void;
}) {
  const LANGS = useMemo(() => allLanguages(), []);
  const [q, setQ] = useState("");
  const [level, setLevel] = useState<Item["level"]>("Conversational");

  const filtered = useMemo(() => {
    const s = q.trim().toLowerCase();
    if (!s) return LANGS.slice(0, 100);
    return LANGS.filter((l) => l.name.toLowerCase().includes(s) || l.code.toLowerCase().includes(s)).slice(0, 100);
  }, [q, LANGS]);

  function add(code: string) {
    if (!code) return;
    if (value.some((v) => v.lang === code)) return;
    onChange([...value, { lang: code, level }]);
    setQ("");
  }
  function setItemLevel(code: string, lvl: Item["level"]) {
    onChange(value.map((v) => (v.lang === code ? { ...v, level: lvl } : v)));
  }
  function remove(code: string) {
    onChange(value.filter((v) => v.lang !== code));
  }

  return (
    <div className="space-y-2">
      <div className="grid gap-2 sm:grid-cols-[1fr_14rem]">
        <input
          value={q}
          onChange={(e) => setQ(e.target.value)}
          className="rounded border border-slate-300 p-2"
          placeholder="Type to search language…"
        />
        <div className="flex gap-2">
          <select value={level} onChange={(e) => setLevel(e.target.value as Item["level"]) } className="rounded border border-slate-300 p-2">
            <option>Basic</option>
            <option>Conversational</option>
            <option>Fluent</option>
            <option>Native</option>
          </select>
          <button
            type="button"
            className="rounded bg-slate-900 px-3 py-2 text-white hover:bg-slate-800"
            onClick={() => add(q.toLowerCase().length === 2 ? q.toLowerCase() : filtered[0]?.code ?? "")}
          >
            Add
          </button>
        </div>
      </div>
      <div className="flex flex-wrap gap-2">
        {value.map((v) => (
          <div key={v.lang} className="flex items-center gap-2 rounded-full border border-slate-300 px-3 py-1">
            <span className="font-medium">{v.lang.toUpperCase()}</span>
            <select value={v.level} onChange={(e) => setItemLevel(v.lang, e.target.value as Item["level"]) } className="text-xs">
              <option>Basic</option>
              <option>Conversational</option>
              <option>Fluent</option>
              <option>Native</option>
            </select>
            <button type="button" onClick={() => remove(v.lang)} className="text-xs text-slate-600 hover:underline">
              Remove
            </button>
          </div>
        ))}
        {value.length === 0 && <p className="text-sm text-slate-500">No additional languages added.</p>}
      </div>
    </div>
  );
}

