"use client";
import { detectCA, isUSZip } from "@/lib/postal";
import type { UseFormRegister, UseFormSetValue, FieldErrors } from "react-hook-form";
import type { RegisterValues } from "@/lib/register-schemas";

export default function PostalInput({
  register,
  setValue,
  errors,
}: {
  register: UseFormRegister<RegisterValues>;
  setValue: UseFormSetValue<RegisterValues>;
  errors: FieldErrors<RegisterValues>;
}) {
  return (
    <label className="block">
      <span className="block text-sm text-slate-700">Postal/ZIP</span>
      <input
        {...register("postal")}
        className="mt-1 w-full rounded border border-slate-300 p-2"
        placeholder="A1A 1A1 or 90210"
        onInput={(e) => {
          const t = e.target as HTMLInputElement;
          // Auto uppercase + single space after 3 for Canada pattern
          let v = t.value.toUpperCase();
          v = v.replace(/[^A-Z0-9\s-]/g, "");
          if (/^[ABCEGHJ-NPRSTVXY]\d[A-Z]/.test(v.replace(/\s/g, ""))) {
            const s = v.replace(/\s+/g, "").slice(0, 6);
            t.value = s.length > 3 ? `${s.slice(0, 3)} ${s.slice(3)}` : s;
          }
        }}
        onBlur={(e) => {
          const raw = (e.target as HTMLInputElement).value;
          const ca = detectCA(raw);
          if (ca.ok) {
            if (ca.normalized) setValue("postal", ca.normalized);
            setValue("country", "CA");
            if (ca.province) setValue("region", ca.province);
            return;
          }
          if (isUSZip(raw)) {
            setValue("country", "US");
          }
        }}
      />
      {errors.postal?.message && <p className="text-xs text-rose-600">{errors.postal.message as string}</p>}
    </label>
  );
}

