"use client";
import { useState } from "react";
import { formatPhoneForDisplay, toE164 } from "@/lib/phone-format";
import type { UseFormRegister, UseFormSetValue, UseFormWatch, FieldErrors } from "react-hook-form";
import type { RegisterValues } from "@/lib/register-schemas";

export default function PhoneInput({
  register,
  setValue,
  watch,
  errors,
}: {
  register: UseFormRegister<RegisterValues>;
  setValue: UseFormSetValue<RegisterValues>;
  watch: UseFormWatch<RegisterValues>;
  errors: FieldErrors<RegisterValues>;
}) {
  const [display, setDisplay] = useState("");
  const phoneCountry = watch("phoneCountry") as "CA" | "US" | undefined;

  return (
    <div className="grid gap-3 sm:grid-cols-[8rem_1fr]">
      <label className="block">
        <span className="block text-sm text-slate-700">Country</span>
        <select
          {...register("phoneCountry")}
          defaultValue="CA"
          className="mt-1 w-full rounded border border-slate-300 p-2"
        >
          <option value="CA">Canada (+1)</option>
          <option value="US">United States (+1)</option>
        </select>
      </label>

      <label className="block">
        <span className="block text-sm text-slate-700">Phone</span>
        <input
          {...register("phone")}
          value={display}
          onChange={(e) => {
            const v = e.target.value;
            setDisplay(formatPhoneForDisplay(v, phoneCountry ?? "CA"));
            setValue("phone", v, { shouldValidate: false });
          }}
          onBlur={(e) => {
            const e164 = toE164(e.target.value, phoneCountry ?? "CA");
            if (e164) {
              setDisplay(e164);
              setValue("phone", e164, { shouldValidate: true });
            }
          }}
          inputMode="tel"
          className="mt-1 w-full rounded border border-slate-300 p-2"
          placeholder="+1 204 555 1234"
        />
        {errors.phone?.message && <p className="text-xs text-rose-600">{errors.phone.message as string}</p>}
      </label>
    </div>
  );
}
